import "core-js/modules/es.array.push.js";
import { ref } from 'vue';
import Plotly from 'plotly.js-dist';
export default {
  data() {
    return {
      model_type: 'none',
      varAge: ref(""),
      varGender: ref("1"),
      varNIHSS: ref(""),
      varDiabetes_mellitus: ref(""),
      varHTCheck: ref([]),
      varHypertension: ref('1'),
      varAtrial_fibrillation: ref(""),
      varTime_from_onset_to_MRI: ref(""),
      varADC_620: ref(""),
      varTmax_4s: ref(""),
      varTmax_6s: ref(""),
      varTmax_8s: ref(""),
      varTmax_10s: ref(""),
      varPWI_DWI_mismatch: ref(""),
      varHIR: ref(""),
      varRisk: ref(''),
      count_practical: -1,
      count_refined: -1
    };
  },
  methods: {
    calRiskDeprecated(var0, var1, var2) {
      let var0sc = (var0 - 64.09811320754717) / 78.48224048733397;
      let var1sc = (var1 - 0.24603773584905667) / 0.2762166200964652;
      let var2sc = (var2 - 8.086792452830188) / 7.304948897212536;
      let result = Math.round(1 / (1 + Math.exp(-(0.3463 * var0sc + 0.0888 * var1sc + 0.0384 * var2sc))) * 10000, 4) / 10000;
      return result;
    },
    onSubmit(values) {
      let var0 = parseFloat(values.var0);
      let var1 = parseFloat(values.var1);
      let var2 = parseFloat(values.var2);
      // this.varRisk = this.$calRisk(var0, var1, var2);
      let HT = {
        x: [],
        y: [],
        z: [],
        mode: 'markers',
        marker: {
          color: 'rgba(255, 0, 0, 0.9)',
          size: 4,
          line: {
            color: 'rgba(255, 0, 0, 0.3)',
            width: 0.5
          },
          opacity: 0.8
        },
        type: 'scatter3d',
        name: 'HT'
      };
      let nonHT = {
        x: [],
        y: [],
        z: [],
        mode: 'markers',
        marker: {
          color: 'rgba(0, 0, 255, 0.9)',
          size: 4,
          line: {
            color: 'rgba(0, 0, 255, 0.3)',
            width: 0.5
          },
          opacity: 0.8
        },
        type: 'scatter3d',
        name: 'non-HT'
      };
      // const star = 'M 10,1 L 12,7 L 18,7 L 13,11 L 15,17 L 10,13 L 5,17 L 7,11 L 2,7 L 8,7 Z';
      let theOne = {
        x: [var0],
        y: [var1],
        z: [var2],
        mode: 'markers',
        marker: {
          color: 'rgba(0, 255, 0, 0.9)',
          size: 6,
          // symbol: "star-triangle-up",
          // line: {
          //   color: 'rgba(0, 255, 0, 0.7)',
          //   width: 0.5,
          // },
          opacity: 0.9
        },
        type: 'scatter3d',
        name: 'UserInput'
      };
      // console.log(theOne);
      for (let dPoint of this.$scatterData) {
        //let risk = this.$calRisk(dPoint.var0, dPoint.var1, dPoint.var2);
        // console.log(dPoint, risk);
        if (dPoint.label === 1) {
          HT.x.push(dPoint.var0);
          HT.y.push(dPoint.var1);
          HT.z.push(dPoint.var2);
        } else if (dPoint.label === 0) {
          nonHT.x.push(dPoint.var0);
          nonHT.y.push(dPoint.var1);
          nonHT.z.push(dPoint.var2);
        } else {
          console.log('你的label怎么回事?', dPoint.label);
        }
      }
      let scatter = document.getElementById('tester');
      Plotly.newPlot(scatter, [HT, nonHT, theOne], {
        margin: {
          l: 0,
          r: 0,
          b: 0,
          t: 0,
          pad: 0
        },
        scene: {
          xaxis: {
            title: 'x: Tmax > 6s',
            linewidth: 4
          },
          yaxis: {
            title: 'y: HIR',
            linewidth: 4
          },
          zaxis: {
            title: 'z: NIHSS score at admission',
            linewidth: 4
          }
        },
        legend: {
          // x: 1,
          // y: 1,
          orentation: 'h',
          traceorder: 'normal',
          size: 10,
          bgcolor: '#E8E8E8',
          bordercolor: '#FFFFFF',
          borderwidth: 2
        }
      });
    },
    onSubmitPractical(values) {
      let Tmax_6s = parseFloat(values.Tmax_6s);
      let HIR = parseFloat(values.HIR);
      let NIHSS = parseFloat(values.NIHSS);
      this.$predict({
        model_type: this.model_type,
        Tmax_6s: Tmax_6s,
        HIR: HIR,
        NIHSS: NIHSS
      }, this);
      // console.log(result);
      this.onSubmit({
        var0: Tmax_6s,
        var1: HIR,
        var2: NIHSS
      });
    },
    onSubmitRefined(values) {
      // console.log(values);
      console.log(this.varHypertension, this.varGender);
      this.$predict({
        model_type: this.model_type,
        Age: parseFloat(values.Age),
        Gender: parseFloat(this.varGender),
        NIHSS: parseFloat(values.NIHSS),
        // Diabetes_mellitus: parseFloat(values.Diabetes_mellitus),
        Hypertension: parseFloat(this.varHypertension),
        // Atrial_fibrillation: parseFloat(values.Atrial_fibrillation),
        Time_from_onset_to_MRI: parseFloat(values.Time_from_onset_to_MRI),
        ADC_620: parseFloat(values.ADC_620),
        Tmax_4s: parseFloat(values.Tmax_4s),
        Tmax_6s: parseFloat(values.Tmax_6s),
        Tmax_8s: parseFloat(values.Tmax_8s),
        Tmax_10s: parseFloat(values.Tmax_10s),
        PWI_DWI_mismatch: parseFloat(values.Tmax_6s) - parseFloat(values.ADC_620),
        HIR: Math.round(parseFloat(values.Tmax_10s) / parseFloat(values.Tmax_6s) * 10) / 10
      }, this);
      // this.onSubmit({
      //   var0: parseFloat(values.Tmax_6s),
      //   var1: parseFloat(values.Tmax_10s)/parseFloat(values.Tmax_6s),
      //   var2: parseFloat(values.NIHSS),
      // })
    }
  },
  name: 'MoCalculator',
  mounted() {
    this.model_type = this.$route.params.type;
    console.log(this.model_type);
    this.count_practical = this.$counts['practical'];
    this.count_refined = this.$counts['refined'];
  }
};
console.log('everywhere');