import { ref } from 'vue';
export default {
  name: 'MoTools',
  data() {
    return {
      example: ref({}),
      pic0: ref(""),
      pic1: ref(""),
      practicalRisk: ref(0),
      refinedRisk: ref(0)
    };
  },
  methods: {
    calRiskDeprecated(var0, var1, var2) {
      let var0sc = (var0 - 64.09811320754717) / 78.48224048733397;
      let var1sc = (var1 - 0.24603773584905667) / 0.2762166200964652;
      let var2sc = (var2 - 8.086792452830188) / 7.304948897212536;
      let result = Math.round(1 / (1 + Math.exp(-(0.3463 * var0sc + 0.0888 * var1sc + 0.0384 * var2sc))) * 10000, 4) / 10000;
      return result;
    }
  },
  mounted() {
    const id = this.$route.params.id;
    const examples = {};
    this.$examples.map(item => {
      examples[item.id] = item;
    });
    this.example = examples[id];
    this.practicalRisk = Math.round(10000 * this.example.practicalRisk) / 10000;
    this.refinedRisk = Math.round(10000 * this.example.refinedRisk) / 10000;
    this.pic0 = this.example.pic0;
    this.pic1 = this.example.pic1;
  }
};