const _hoisted_9 = /*#__PURE__*/_createStaticVNode("<section data-v-60bdaa59><p data-v-60bdaa59><h3 data-v-60bdaa59>BACKGROUND AND PURPOSE</h3><p data-v-60bdaa59> Hemorrhagic transformation (HT) following acute ischemic stroke (AIS) critically affects patient prognosis. This study aims to develop reliable machine learning (ML) models for predicting HT in AIS patients using magnetic resonance imaging (MRI). </p></p><p data-v-60bdaa59><h3 data-v-60bdaa59>METHODS</h3><p data-v-60bdaa59> We enrolled 379 AIS patients, 131 with HT and 248 without. All underwent MRI, including diffusion-weighted imaging (DWI) and perfusion-weighted imaging (PWI), within 24 hours of symptom onset, and had follow-up CT or MRI within 14 days. Clinical baseline features and MRI parameters (volume of ADC &lt; 620 × 10 <sup data-v-60bdaa59>−6</sup> mm2/s, Tmax &gt; 4 s, 6 s, 8 s, 10 s, PWI-DWI mismatch area, and hypoperfusion intensity ratio [HIR]) were collected. Patients were randomly split into a training set (70%, n=265; 92 with HT) and a testing set (30%, n=114; 39 with HT). Six ML algorithms—Logistic Regression, Support Vector Machine, Decision Tree, XGBoost, LightGBM, and Random Forest—were trained and optimized using 5-fold cross-validation. 1,000 times’ shuffling was performed for the training set of each baseline model. Various fusion models were created, including soft voting, stacking, and integrating prior key patient characteristics into robust baseline models. The best-performing model was selected based on testing set performance. </p></p><p data-v-60bdaa59><h3 data-v-60bdaa59>RESULTS</h3><p data-v-60bdaa59> The Logistic Regression-based model was chosen as the practical model with a relatively high area under the curve (AUC) of 0.907 for its simplicity and efficiency by utilizing only three characteristics: the volume of Tmax &gt; 6 s, the National Institutes of Health Stroke Scale score at admission, and HIR with feature importance scores of 0.7507, 0.2269, and 0.0996, respectively. The refined fusion model, by utilizing HIR as a prior stratification parameter with cutoff value set as 0.5 and two robust baseline models (XGBoost and LightGBM), achieved the highest AUC of 0.9161 with an accuracy of 0.8947 among all fusion models and exceeded all baseline models. Both the practical and refined models are accessible at <a style=\"text-decoration:underline;color:black;\" href=\"https://yike-wood.github.io/HT-Predict/\" data-v-60bdaa59>https://yike-wood.github.io/HT-Predict/</a>. </p></p><p data-v-60bdaa59><h3 data-v-60bdaa59>CONCLUSIONS</h3><p data-v-60bdaa59> Both the practical and the refined ML model, leveraging MRI-DWI and PWI, reliably predict HT in AIS patients with high sensitivity and specificity, potentially aiding radiologists and clinicians in timely HT risk assessment post-MRI. </p></p></section>", 1);
