import { createApp } from 'vue'
import App from './App.vue'
import router from './router'
import 'vant/lib/index.css'
import axios from 'axios';

async function fetchData() {
  try {
    const response = await fetch('./assets/example.json');
    if (!response.ok) {
      throw new Error('Network response was not ok');
    }
    const data = await response.json();
    return data.examples;
  } catch (error) {
    console.error('There was a problem with the fetch operation:', error);
    return [];
  }
}

async function fetchUtils() {
  try {
    const response = await fetch('./assets/utils.json');
    if (!response.ok) {
      throw new Error('Network response was not ok');
    }
    const utils = await response.json();
    return utils.calRisk;
  } catch (error) {
    console.error('There was a problem with the fetch operation:', error);
    return [];
  }
}


async function fetchScatter() {
  try {
    const response = await fetch('./assets/scatterData.json');
    if (!response.ok) {
      throw new Error('Network response was not ok');
    }
    const scatterData = await response.json();
    return scatterData;
  } catch (error) {
    console.error('There was a problem with the fetch operation:', error);
    return [];
  }
}

async function fetchURL() {
  try {
    const response = await fetch('./assets/url.json');
    if (!response.ok) {
      throw new Error('Network response was not ok');
    }
    const urls = await response.json();
    
    return urls.url;
  } catch (error) {
    console.error('There was a problem with the fetch operation:', error);
    return [];
  }
}

const app = createApp(App);
// const url = 'http://localhost:38002/'
async function initializeApp() {
    const url = await fetchURL();
    // console.log(url)
    const examples = await fetchData();
    const calRiskStr = await fetchUtils();
    const scatterData = await fetchScatter();
    // console.log(examples);
    // console.log(calRiskStr);
    // console.log(scatterData);
    app.config.globalProperties.$examples = examples;
    app.config.globalProperties.$calRisk = eval(calRiskStr.join('\n'));
    app.config.globalProperties.$predict = function(payload, that=null){
      let queryJson = {
        type: payload.model_type,
        record: {
          Age: payload.Age || 0,
          Gender: payload.Gender || 0,
          NIHSS: payload.NIHSS || 0,
          Diabetes_mellitus: payload.Diabetes_mellitus || 0,
          Hypertension: payload.Hypertension || 0,
          Atrial_fibrillation: payload.Atrial_fibrillation || 0,
          Time_from_onset_to_MRI: payload.Time_from_onset_to_MRI || 0,
          ADC_620: payload.ADC_620 || 0,
          Tmax_4s: payload.Tmax_4s || 0,
          Tmax_6s: payload.Tmax_6s || 0,
          Tmax_8s: payload.Tmax_8s || 0,
          Tmax_10s: payload.Tmax_10s || 0,
          PWI_DWI_mismatch: payload.PWI_DWI_mismatch || 0,
          HIR: payload.HIR || 0,
        },
      };
      axios.post(url+'predict', 
          queryJson,)
        .then(response => {
          // console.log(response);
          that.varRisk = response.data.probability.toFixed(6);
          that['count_'+payload.model_type] = response.data[payload.model_type]
        }).catch(error => {
          this.error = error;
          console.error(error);
        })
    };
    
    app.config.globalProperties.$scatterData = scatterData;
    axios.post(url+'hello')
      .then(response=>{
        let counts = response.data;
        // console.log(counts);
        app.config.globalProperties.$counts = counts;
        app.use(router).mount('#app')
      }).catch(error=>{
        console.error(error);
        app.config.globalProperties.$counts = {
          'home': -1, 
          'practical': -1,
          'refined': -1
        }
        app.use(router).mount('#app')
    })
    
}

initializeApp();

